import React, { Component, Suspense } from 'react';
import { Dropdown, Accordion, Nav, Tab, ToggleButtonGroup, ToggleButton, OverlayTrigger, Tooltip } from 'react-bootstrap';

class EmbedList extends Component {
    render() {
        const { state } = this.props
        return <div className="filter_list accordion">
            {state.collectionList.length > 0 && state.publishList.length > 0 && state.publishList.map((item, pIndex) => {
                return <div className="accordion-item"> <div className="menu-item" key={pIndex}>

                    <button type="button" aria-expanded="false" class="accordion-button collapsed">
                        <div className="d-flex justify-content-between align-items-center w-100" onClick={event => this.props.onUpdateReponseData({ layout: item.layout, images: item.images })}>
                            <span className="menu-title fw-bold text-ellipsis d-block me-3 fs-8">{item.name}</span>
                            <div className="d-flex align-items-center justify-content-end">

                                <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">Remove</Tooltip>}>
                                    <button type="button" className="btn btn-icon btn-secondary h-28px w-28px"
                                        onClick={this.props.onClickToDelete({ "id": item.id })}>
                                        <span className="svg-icon svg-icon-6 me-0">
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z" />
                                                <path d="M7 4V2h10v2h5v2h-2v15a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V6H2V4h5zM6 6v14h12V6H6zm3 3h2v8H9V9zm4 0h2v8h-2V9z" fill="currentColor" />
                                            </svg>
                                        </span>
                                    </button>
                                </OverlayTrigger>
                            </div>
                        </div></button>
                </div></div>

            })
            }
        </div >
    }

}
export default EmbedList

